import axios from 'axios';

// If enable, then it will load static data. Otherwise, it will load from remote backend.
// Currently, it does not support remote backend mode. 
const ENABLE_STATIC_JSON = true;
export const ENABLE_FORCE_AUTH = false;
// Remote Backend.
const URL = process.env.NODE_ENV === 'development'
    ? 'http://localhost:7777'
    : window.location.origin;
const axiosInstance1 = axios.create({
    baseURL: `${URL}/api/`,
    // timeout: 1000,
    headers: {
        'Access-Control-Allow-Origin': '*'
    }
});

// Load Static Data.
const URL2 = window.location.href;
//const URL2 = window.location.origin;
const axiosInstance2 = axios.create({
    baseURL: `${URL2}/data/`,
    // timeout: 1000,
    headers: {
        'Access-Control-Allow-Origin': '*'
    }
});

let axiosInstance = (ENABLE_STATIC_JSON)?axiosInstance2:axiosInstance1;
let TOKEN:any = "";


// Read graph dataset metainfo.
export async function getUserVerify(username: any, password:any): Promise<any> {
    let url = `/user_verify`;
    const params = { username, password};
    const res = await axiosInstance.get(url, {params}); // EXCEPTION
    //const params = { classifier_start, classifier_end };
    if (res.status === 200) {
        return res.data;
    }
    throw res;
}
function wrap_params_with_token(params: any){
    if(ENABLE_FORCE_AUTH){
        const new_params:any = {
            token: TOKEN,
            ...params
        };
        return new_params;
    }else{
        return params;
    }
    
    
    
}
export function setToken(token:any) {
    TOKEN = token;
}

// Read graph dataset metainfo.
export async function getData(): Promise<any> {
    let url = `/data`;
    if(ENABLE_STATIC_JSON){
        url = '/data.json'
    }
    //const params = { classifier_start, classifier_end };
    const params = wrap_params_with_token({});

    const res = await axiosInstance.get(url, {params});
    if (res.status === 200) {
        return res.data;
    }
    throw res;
}

