export async function getExploreIndividualData(dataset:any="PASCAL"): Promise<any> {
    let url = `/` + dataset + "/explore_" + dataset + ".json";
    if(ENABLE_STATIC_JSON){
        url = `/` + dataset + "/explore_" + dataset + ".json";
    }
    //const params = { classifier_start, classifier_end };
    const params = wrap_params_with_token({});

    const res = await axiosInstance.get(url, {params});
    if (res.status === 200) {
        return res.data;
    }
    throw res;
}
// Read graph dataset metainfo.
export async function getRawData(dataset:any="PASCAL", filename="2008_000348.jpg"): Promise<any> {
    let url = `/` + dataset + "/" + filename;
    if(ENABLE_STATIC_JSON){
        url = `/` + dataset + "/" + filename;
    }
    //const params = { classifier_start, classifier_end };
    const params = wrap_params_with_token({});

    const res = await axiosInstance.get(url, {params});
    if (res.status === 200) {
        return res.data;
    }
    throw res;
}

