export function getRawDataURL(dataset:any="PASCAL", filename="2008_000348.jpg"){
    let url = `${URL}/api/` + `/` + dataset + "/" + filename;
    if(ENABLE_STATIC_JSON){
        url = `${URL2}/data/` + `/` + dataset + "/" + filename;
    }
    return url;
}