function get_boundingbox(graph_layout:any[]){
    if(graph_layout.length === 0){
        return {
            "xmin":0,
            "xmax":0,
            "ymin":0,
            "ymax":0
        }
    }else{
        let xmin = graph_layout[0][0];
        let xmax = graph_layout[0][0];
        let ymin = graph_layout[0][1];
        let ymax = graph_layout[0][1];
        for(let i = 0; i< graph_layout.length; i++){
            let nowx = graph_layout[i][0];
            let nowy = graph_layout[i][1];
            if(xmin > nowx){
                xmin = nowx;
            }
            if(xmax < nowx){
                xmax = nowx;
            }
            if(ymin > nowy){
                ymin = nowy;
            }
            if(ymax < nowy){
                ymax = nowy;
            }
        }
        return {
            "xmin":xmin,
            "xmax":xmax,
            "ymin":ymin,
            "ymax":ymax
        }
    }
}
function transform_graphlayout(graph_layout:any[], width:number, height:number){
    if(graph_layout.length === 0){
        return graph_layout;
    }else{
        let bounding_box = get_boundingbox(graph_layout);
        //let canvas_centerx = 300;
        //let canvas_centery = 300;
        //let width = Swidth;
        //let height = Sheight;
        let margin = 20;
        if(graph_layout.length >= 100){
            margin = 20;
        }
        
        let realwidth = width - 2*margin;
        let realheight = height - 2*margin;
        let gap_x = bounding_box["xmax"] - bounding_box["xmin"];
        let gap_y = bounding_box["ymax"] - bounding_box["ymin"];
        if(gap_x === 0){
            gap_x = 1e-16;
        }
        if(gap_y === 0){
            gap_y = 1e-16;
        }
        let realscale = Math.min(realwidth / gap_x, realheight / gap_y);
        let left = margin + (realwidth - realscale * gap_x) / 2;
        let top = margin + (realheight - realscale * gap_y) / 2; 
        let xmin = bounding_box["xmin"];
        let ymin = bounding_box["ymin"];
        let new_graph_layout = [];
        for(let i = 0; i< graph_layout.length; i++){
            let nowx = graph_layout[i][0];
            let nowy = graph_layout[i][1];
            let locx = left+ (nowx - xmin) * realscale;
            let locy = top + (nowy - ymin) * realscale;
            new_graph_layout.push([locx,locy]);
        }
        return new_graph_layout;
    }
}
function skew_weight(weight:any, range_min:any=0.1, range_max:any=1){
    // Assume weight is [0,1]
    return (weight - 0) * 0.9 + range_min;
}

function assert_key(pkg:any, key_list:any, verbose:boolean=false, pkg_name:any = ""){
    let flag = true;
    let pkg_key_list = Object.keys(pkg);
    for(let i = 0; i<key_list.length; i++){
        if(pkg_key_list.indexOf(key_list[i]) >= 0){

        }else{
            flag =  false;
            if(verbose){
                
                let message = ""+key_list[i]+" is required.";
                if(pkg_name){
                    message = ""+key_list[i]+" is required in " + pkg_name +" .";
                }
                console.log(message);
            }
        }
    }
    return flag;
}
function assert_key_hier(pkg:any, key_hier_list:any, verbose:boolean=true, pkg_name:any = ""){
    let filtered_key_hier_list = key_hier_list.filter((d:any)=>{
        return Object.keys(d).indexOf("n")>=0;
    });
    let first_level_key_list = filtered_key_hier_list.map((d:any)=>{
        return d["n"];
    });
    let first_key_flag = assert_key(pkg, first_level_key_list, verbose, pkg_name);
    if(first_key_flag){
        let flag = true;
        for(let i = 0; i<filtered_key_hier_list.length; i++){
            let name = filtered_key_hier_list[i]["n"];
            if(Object.keys(filtered_key_hier_list[i]).indexOf("c")>=0){
                let children = filtered_key_hier_list[i]["c"];
                let local_flag = assert_key_hier(pkg[name], children, verbose, pkg_name+"."+name);
                if(!local_flag){
                    flag = false;
                }
            }
        }
        return flag;
    }
    return false;
    
}
function removeAllChildNodes(parent:any) {
    while (parent.firstChild) {
        parent.removeChild(parent.firstChild);
    }
}
// Get text width before rendering
const getTextWidth = (text:any, font:any) => {
    const element = document.createElement('canvas');
    const context:any = element.getContext('2d');
    context.font = font;
    return context.measureText(text).width;
}

